* {
  box-sizing: border-box;
}

@base_font_size: 16px;
@max_content_size: 700px;

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: @base_font_size;
  margin: 0px;
}

header {
  padding: 60px 40px 40px;
  max-width: @max_content_size;
  margin: 0px auto;
}

h1,
h2,
h3 {
  margin: 0px;

  a {
    color: inherit;
  }
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.1em;
}

ul {
  list-style: none;
  padding: 5px 0px;
  margin: 0px;
}

li {
  a {
    display: block;
    color: inherit;
    padding: 5px 0px;
  }
}

.logo {
  max-width: @max_content_size / 8;
  padding-bottom: 20px;
}

.section-group {
  max-width: @max_content_size;
  margin: 0px auto;
  padding-left: 40px;
  padding-right: 40px;

  &--nested {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

section {
  padding-top: 10px;
  padding-bottom: 10px;
}

.equal-split {
  display: flex;
  justify-content: space-between;
  width: 90%;
}

.updates {
  p {
    max-width: 260px;
    margin: 0.5em 0;
  }
}

footer {
  max-width: @max_content_size;
  margin: 0px auto;
  padding: 40px;

  p {
    font-size: 0.9em;
    margin: 0px;
  }
}

@media (max-width: 600px) {
  h1 {
    font-size: 1.4em;
  }

  h2 {
    font-size: 1.2em;
  }

  h3 {
    font-size: 1em;
  }

  .logo {
    width: 48px;
  }

  .equal-split {
    display: block;
    width: auto;
  }

  header {
    padding: 25px;
  }

  .section-group,
  footer {
    padding-left: 25px;
    padding-right: 25px;
  }
}
